import { GAppDM } from "../../DataModel/AppDataModel";
import { core } from "../../Framework/Core/Base";
import { GHelper } from "../../Framework/Logic/Helper";
import { Page } from "../../Framework/View/Page/Page";
import { GPageMgr } from "../../Framework/View/Page/PageManager";
import { GUserMgr } from "../../Logic/UserManager";
import { GHallHttp } from "../../Net/HallHttp";
import { BagItemItem, EBagItemState, GItemCfg } from "../../Service/ItemService";
import { GMatchSer } from "../../Service/MatchService";
import { GMsgCenter, MessageCenter } from "../../utils/MessageCenter";
import { ykUtils } from "../../utils/YkUtils";
import Widget_NumberFrame from "../Widget/Widget_NumberFrame";

const {ccclass, property} = cc._decorator;

@ccclass
export default class Page_ChangePoint extends Page {
   

    @property(cc.Label)
    lab_title_0:cc.Label = null;    // 
    @property(cc.Label)
    lab_title_1:cc.Label = null;    // 
    @property(cc.Label)
    lab_title_2:cc.Label = null;    // 

    @property(Widget_NumberFrame)
    numberFrame = null
    private _itemData = null;
    onLoad(): void {
        this.numberFrame.setOnEditEndCb(()=>{
            this.refreshView()
        })
    }

    bt_close()
    {
        GPageMgr.closePage('Page_ChangePoint');
    }
    async bt_confirm()
    {
        
       if(this._itemData.count == 1){
            let res =  await GHallHttp.request("exchangeRewardCoin",{id:this._itemData.id})
            if(res.errcode)
            {
                GHelper.showTip(JSON.stringify(res.errcode))
                return
            }
           
            GHelper.showTip("");
            await GUserMgr.refreshSelfUser()
        
            await GItemCfg.init();
            // GMsgCenter.send("update_myprize");
            GMsgCenter.send('update_myprize',{tag:'Page_MyPrizeDetail',item_id:this._itemData.item_id})
            GMsgCenter.send("updateUserInfo");

            GPageMgr.closePage("Page_ChangePoint");
       }else{   
            let num = this.numberFrame.getCurNum()
            let res =  await GHallHttp.request("exchangeRewardCoinMore",{item_id:this._itemData.item_id,num:num})
            if(res.errcode)
            {
                GHelper.showTip(JSON.stringify(res.errcode))
                GPageMgr.closePage("Page_Change")
            }
            else
            {
                // n n 
                await GUserMgr.refreshSelfUser()
                
                let item_id = this._itemData.item_id
                GPageMgr.openPage("Page_Tips",{
                    tips:""+res.allNum+","+""+res.successNum+'',
                    buttons:[
                        {
                            label:"",
                            callback:async ()=>
                            {
                                // 
                               
                                GMsgCenter.send('update_myprize',{tag:'Page_MyPrizeDetail',item_id:item_id})
                               
                                GMsgCenter.send("updateUserInfo");
                            }
                        }
                    ]
                })

                GPageMgr.closePage("Page_ChangePoint")
            }
       }
    }




    refreshView()
    {
        let itemM = GItemCfg.getItemById(this._itemData.item_id)
        let curNum = this.numberFrame.getCurNum()
        this.lab_title_0.string = ""+curNum+' '+itemM.name+" "
        this.lab_title_1.string = (itemM['coin']*curNum) +" "
        this.lab_title_2.string = ",("+GUserMgr.self_user.reward_coin+")"
    }

   
    onOpen(itemData:any)
    {
        let itemM = GItemCfg.getItemById(itemData.item_id)
        if(!itemM || !itemM.id || !itemM['coin'])
        {
            GPageMgr.closePage("Page_ChangePoint");
            GHelper.showTip("")
            return;
        }
        this._itemData = itemData;
        this.numberFrame.setMax(this._itemData.count)
        this.refreshView()
    }
}
