export enum ITEM_TYPE {
    UNKNOWN = 0,
    PROP = 1,           //
    CARD = 2,           //
    HERO = 3,           //
    WEAPON = 4,         //
    SKIN = 5,           //
}

export enum PROP {
    UNKNOWN = 0,
    COIN = 101,
    DIAMOND = 102,
    SCORE = 103,
    TICKET_1 = 201,
    TICKET_2 = 202,
    TICKET_3 = 203,
}

export class ItemInfo {
    type: ITEM_TYPE;
    id: number;
    count: number;

    constructor(type: number = 0, id: number = 0, count: number = 0) {
        this.type = type;
        this.id = id;
        this.count = count;
    }
}

//"101=100,102=10,201=5" => ItemInfo[]
function parseItems(data: string) {
    let items = data ? data.split(',') : [];
    return items.map((item) => {
        let [id, count] = item.split('=');
        return new ItemInfo(ITEM_TYPE.PROP, Number(id), Number(count));
    })
}

//"101=100,102=10,201=5" => {101:100, 102:10, 103:5}
function parseObject(data: string) {
    let items = data ? data.split(',') : [];
    let obj = {};
    items.forEach(item => {
        let [key, value] = item.split('=');
        obj[key] = Number(value);
    })
    return obj;
}

//12:34:56 => {hour:12, minute:24, second:56}
function parseTime(data: string) {
    let [hour, minute, second] = data ? data.split(':') : [];
    return {
        hour: Number(hour) || 0,
        minute: Number(minute) || 0,
        second: Number(second) || 0
    }
}

